/**
 * Utilitaire pour g√©rer les ann√©es acad√©miques
 * @module utils/academicYear
 */

/**
 * Obtient l'ann√©e acad√©mique actuelle au format "YYYY-YYYY"
 * 
 * Logique :
 * - Si on est entre septembre et d√©cembre : ann√©e en cours - ann√©e suivante
 * - Si on est entre janvier et ao√ªt : ann√©e pr√©c√©dente - ann√©e en cours
 * 
 * Exemples :
 * - Octobre 2025 ‚Üí "2025-2026"
 * - Mars 2025 ‚Üí "2024-2025"
 * - Septembre 2025 ‚Üí "2025-2026"
 * 
 * @returns {string} Ann√©e acad√©mique au format "YYYY-YYYY"
 */
export const getCurrentAcademicYear = () => {
  const now = new Date();
  const currentYear = now.getFullYear();
  const currentMonth = now.getMonth() + 1; // JavaScript months are 0-indexed
  
  // Si on est entre septembre (9) et d√©cembre (12), l'ann√©e acad√©mique commence cette ann√©e
  // Sinon, elle a commenc√© l'ann√©e pr√©c√©dente
  if (currentMonth >= 9) {
    return `${currentYear}-${currentYear + 1}`;
  } else {
    return `${currentYear - 1}-${currentYear}`;
  }
};

/**
 * Obtient l'ann√©e acad√©mique suivante
 * 
 * @param {string} [academicYear] - Ann√©e acad√©mique de r√©f√©rence (optionnel)
 * @returns {string} Ann√©e acad√©mique suivante au format "YYYY-YYYY"
 * 
 * @example
 * getNextAcademicYear() // Si on est en 2025 ‚Üí "2026-2027"
 * getNextAcademicYear("2024-2025") ‚Üí "2025-2026"
 */
export const getNextAcademicYear = (academicYear) => {
  const year = academicYear || getCurrentAcademicYear();
  const [startYear] = year.split('-').map(Number);
  return `${startYear + 1}-${startYear + 2}`;
};

/**
 * Obtient l'ann√©e acad√©mique pr√©c√©dente
 * 
 * @param {string} [academicYear] - Ann√©e acad√©mique de r√©f√©rence (optionnel)
 * @returns {string} Ann√©e acad√©mique pr√©c√©dente au format "YYYY-YYYY"
 * 
 * @example
 * getPreviousAcademicYear() // Si on est en 2025 ‚Üí "2024-2025"
 * getPreviousAcademicYear("2025-2026") ‚Üí "2024-2025"
 */
export const getPreviousAcademicYear = (academicYear) => {
  const year = academicYear || getCurrentAcademicYear();
  const [startYear] = year.split('-').map(Number);
  return `${startYear - 1}-${startYear}`;
};

/**
 * G√©n√®re une liste d'ann√©es acad√©miques
 * 
 * @param {number} [count=5] - Nombre d'ann√©es √† g√©n√©rer
 * @param {boolean} [includePast=true] - Inclure les ann√©es pass√©es
 * @param {boolean} [includeFuture=true] - Inclure les ann√©es futures
 * @returns {Array<{value: string, label: string}>} Liste d'ann√©es acad√©miques
 * 
 * @example
 * getAcademicYearOptions(3) 
 * // [
 * //   { value: "2024-2025", label: "Ann√©e 2024-2025" },
 * //   { value: "2025-2026", label: "Ann√©e 2025-2026" },
 * //   { value: "2026-2027", label: "Ann√©e 2026-2027" }
 * // ]
 */
export const getAcademicYearOptions = (count = 5, includePast = true, includeFuture = true) => {
  const currentYear = getCurrentAcademicYear();
  const [startYear] = currentYear.split('-').map(Number);
  const options = [];
  
  const pastCount = includePast ? Math.floor(count / 2) : 0;
  const futureCount = includeFuture ? Math.floor(count / 2) : 0;
  
  // Ann√©es pass√©es
  if (includePast) {
    for (let i = pastCount; i > 0; i--) {
      const year = `${startYear - i}-${startYear - i + 1}`;
      options.push({ value: year, label: `Ann√©e ${year}` });
    }
  }
  
  // Ann√©e en cours
  options.push({ value: currentYear, label: `Ann√©e ${currentYear} (en cours)` });
  
  // Ann√©es futures
  if (includeFuture) {
    for (let i = 1; i <= futureCount; i++) {
      const year = `${startYear + i}-${startYear + i + 1}`;
      options.push({ value: year, label: `Ann√©e ${year}` });
    }
  }
  
  return options;
};

/**
 * V√©rifie si une ann√©e acad√©mique est l'ann√©e en cours
 * 
 * @param {string} academicYear - Ann√©e acad√©mique √† v√©rifier
 * @returns {boolean} True si c'est l'ann√©e en cours
 * 
 * @example
 * isCurrentAcademicYear("2025-2026") // true si on est en octobre 2025
 */
export const isCurrentAcademicYear = (academicYear) => {
  return academicYear === getCurrentAcademicYear();
};

/**
 * Obtient les dates de d√©but et fin d'une ann√©e acad√©mique
 * 
 * @param {string} [academicYear] - Ann√©e acad√©mique (optionnel, par d√©faut l'ann√©e en cours)
 * @returns {{startDate: Date, endDate: Date}} Dates de d√©but et fin
 * 
 * @example
 * getAcademicYearDates("2025-2026")
 * // { startDate: Date("2025-09-01"), endDate: Date("2026-07-31") }
 */
export const getAcademicYearDates = (academicYear) => {
  const year = academicYear || getCurrentAcademicYear();
  const [startYear, endYear] = year.split('-').map(Number);
  
  return {
    startDate: new Date(startYear, 8, 1), // 1er septembre (month is 0-indexed)
    endDate: new Date(endYear, 6, 31) // 31 juillet
  };
};

/**
 * Formate une ann√©e acad√©mique pour l'affichage
 * 
 * @param {string} academicYear - Ann√©e acad√©mique
 * @param {boolean} [showCurrent=false] - Afficher "(en cours)" si c'est l'ann√©e actuelle
 * @returns {string} Ann√©e format√©e
 * 
 * @example
 * formatAcademicYear("2025-2026", true) // "2025-2026 (en cours)"
 */
export const formatAcademicYear = (academicYear, showCurrent = false) => {
  if (showCurrent && isCurrentAcademicYear(academicYear)) {
    return `${academicYear} (en cours)`;
  }
  return academicYear;
};
